import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { DataPage, IDataPageResult, RxDataPageFactoryService } from '@helix/platform/shared/api';
import { IRulePoolStatistics } from './rule-pool-management.interfaces';

@Injectable({
  providedIn: 'root'
})
export class RxRulePoolDataService {
  private resource: DataPage;

  constructor(private rxDataPageService: RxDataPageFactoryService) {
    this.resource = this.rxDataPageService.withType(
      'com.bmc.arsys.rx.application.rule.datapage.RulePoolStatisticsDataPageQuery'
    );
  }

  getRulePoolStatistics(): Observable<IRulePoolStatistics[]> {
    return this.resource.get().pipe(map((dataPageResult: IDataPageResult<IRulePoolStatistics>) => dataPageResult.data));
  }
}
